<template>
          <li class="catalog__item">
          <router-link class="catalog__pic" :to="{name:'product', params: {id: product.id}}">
            <img :src="product.image" :alt="product.title">
          </router-link>

          <h3 class="catalog__title">
            <a href="#">
              {{product.title}}
            </a>
          </h3>

          <span class="catalog__price">
            {{product.price | numberFormat}} ₽
          </span>

          <ul class="colors colors--black" v-if="product.colors.length !== 0">
            <li class="colors__item" v-for="color in product.colors" :key="color.id">
              <label class="colors__label">
                <input class="colors__radio sr-only" type="radio" :value="color.code.toUpperCase()" v-model="currentColor">
                <span class="colors__value" :style="`background-color: ${color.code.toUpperCase()}`">
                </span>
              </label>
            </li>
          </ul>
        </li>
</template>
<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import router from '@/router';

export default {
  data() {
    return {
      currentColor: this.product.colors.length ? this.product.colors[0].code.toUpperCase() : '',
    };
  },
  filters: {
    numberFormat,
  },
  methods: {
    gotoPage,
  },
  props: ['product'],
  components: { router },
};
</script>
