<template>
<div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click="count(productAmount - 1)">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">

                <button type="button" aria-label="Добавить один товар" @click="count(productAmount + 1)" >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>
</template>
<script>

export default {
  model: {
    prop: 'productAmount',
    event: 'count',
  },
  props: ['productAmount'],
  methods: {
    count(productAmount) {
      if (productAmount < 1) return;
      this.$emit('count', productAmount);
    },
  },
};
</script>
