<template>
  <ul class="catalog__pagination pagination">
        <li class="pagination__item">
          <a href="#" class="pagination__link pagination__link--arrow"
          :class="{'pagination__link--disabled':page <= 1}"
            aria-label="Предыдущая страница" @click.prevent="paginate(page-1)">
            <svg width="8" height="14" fill="currentColor">
              <use xlink:href="#icon-arrow-left"></use>
            </svg>
          </a>
        </li>
        <li class="pagination__item" :key="1" v-if="rangePages[0] >= 2">
          <a href="#" class="pagination__link" @click.prevent="paginate(1)">
           {{1}}
          </a>
        </li>
        <li class="pagination__item" v-if="rangePages[0] > 2">
          <span class="pagination__link">
            ...
          </span>
        </li>
        <li class="pagination__item" v-for="pageNumber in rangePages" :key="pageNumber">
          <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
           {{pageNumber}}
          </a>
        </li>
        <li class="pagination__item" v-if="rangePages[rangePages.length-1] < pages-1">
          <span class="pagination__link">
            ...
          </span>
        </li>
        <li class="pagination__item" :key="pages" v-if="rangePages[rangePages.length-1] <= pages-1">
          <a href="#" class="pagination__link" @click.prevent="paginate(pages)">
           {{pages}}
          </a>
        </li>
        <li class="pagination__item">
          <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled':page>=pages}" href="#" aria-label="Следующая страница" @click.prevent="paginate(page+1)">
            <svg width="8" height="14" fill="currentColor">
              <use xlink:href="#icon-arrow-right"></use>
            </svg>
          </a>
        </li>
      </ul>
</template>
<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
    rangePages() {
      let fPage = this.page;
      for (let i = 0; i < 3 && fPage > 1; i++) {
        fPage = this.page - i;
      }
      const arr = [];
      for (let i = fPage; i <= this.page; i++) {
        arr.push(i);
      }
      let lPage = this.page;
      for (let i = 0; i < 3 && lPage < this.pages; i++) {
        lPage = this.page + i;
      }
      for (let i = this.page + 1; i <= lPage; i++) {
        arr.push(i);
      }
      return arr;
    },
  },
  methods: {
    paginate(page) {
      if (page > this.pages) return;
      if (page < 1) return;
      this.$emit('paginate', page);
    },
  },
};
</script>
