<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{this.$store.state.cartProducts.length}} {{this.$store.state.cartProducts.length | productWord}}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" :error="formError.name" v-model="formData.name" placeholder="Введите ваше полное имя"/>

            <BaseFormText title="Адрес доставки" :error="formError.address" v-model="formData.address" placeholder="Введите ваш адрес"/>

            <BaseFormText title="Телефон" :error="formError.phone" v-model="formData.phone" placeholder="Введите ваш телефон" type="tel"/>

            <BaseFormText title="Email" :error="formError.email" v-model="formData.email" placeholder="Введи ваш Email" type="email"/>

            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>{{this.$store.state.delivery | numberFormat}} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <Loading v-if="this.$store.state.cartLoading"/>
            <div class="cart__list" v-else-if="this.$store.state.cartLoadingFailed">
              Ошибка загрузки
            </div>
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{item.product.title}}</h3>
              <b>{{(item.product.price * item.amount) | numberFormat}} ₽</b>
              <span>Артикул: {{item.productId}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{this.$store.state.delivery | numberFormat}} ₽</b></p>
            <p>Итого: <b>{{this.$store.state.cartProducts.length}}</b> {{this.$store.state.cartProducts.length | productWord}} на сумму <b>{{(totalPrice + this.$store.state.delivery) | numberFormat}} ₽</b></p>
          </div>
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div v-if="formLoading"><Loading/></div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import Loading from '@/components/Loading.vue';
import { API_BASE_URL } from '@/config';
import cartPageMixin from '@/mixins/cartPageMixin';
import axios from 'axios';

export default {
  mixins: [cartPageMixin],
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formLoading: false,
    }
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formLoading = true;
      axios.post(
        API_BASE_URL + '/api/orders',
        {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response)=> {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data );
          this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(()=>{
          this.formLoading = false;
        })
    }
  }
}
</script>
